.address {
    .address-row {
        display: flex;
    }
    address {
        padding-right: 40px;
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }
    .contacts-list {
        
        li {
            margin: 0 0 15px -14px;

            &:before {
                display: none;
            }
        }

        a {
            display: flex;
            align-items: center;
        }
    }

    .address-row {
        background: #10100E;
        border-radius: 15px;
        padding: 40px;
    }
    .map-box {
        display: flex;
        width: 50%;
        transition: .3s;
        &:hover {
            opacity: .9;
            filter:  contrast(110%);
        }
    }
}
