
/*tab*/
.tabs-nav,
.content-box {
   
    margin-bottom:30px;
    
}

 
.tabs-nav {
    display: flex;
}
.tab-button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 18px 40px;
    margin-right: 30px;
    line-height: 1.3;
    cursor: pointer;
    &.active {
        color: $inputs;
        background-color: $accent;
    }
    &:last-child {
        margin-right: 0;
    }
}
 
.tab-content {
    display: none;
    border-radius: 0 0 5px 5px;

    .transfer {
        display: flex;
        background: #10100E;
        border-radius: 16px;
        padding: 40px 50px 30px;
       height: 500px;
        picture {
            margin-left: auto;
            max-width: 530px;
        }
    }
    .transfer-title {
        margin-top: 25px;
    }
    .transfer-link {
        font-size: 24px;
        line-height: 30px;
        padding: 48px 40px;
        border-radius: 12px;
        display: flex;
        background-color: #B1FF5C;
        color: #000;
        font-weight: 900;
        justify-content: center;
        align-items: center;
        margin-top: 46px;
        text-decoration: none;
        &:hover {
            background-color: $accent-hover;
        }
    }
    .transfer-text {
        max-width: 650px;
    }

}

.tab-content.active {
    display: block;
}