.wp-pagenavi {
    display: flex;
    align-items: center;
}

.pagination {
    a {
        margin: 0 30px;
        cursor: pointer;
        text-decoration: none;

        &:hover {
            cursor: pointer;
            opacity: .8;
        }
    }

    .previouspostslink,
    .nextpostslink {
        display: inline-block;
        width: 14px;
        height: 24px;
        background: url(assets/img/pagination.svg) center/contain no-repeat;
    }

    .first,
    .last {
        display: inline-block;
        width: 24px;
        height: 24px;
        background: url(assets/img/pagination.svg) 0 /contain round;
    }

    .first,
    .previouspostslink {
        transform: rotate(180deg);
    }


    .current {
        font-size: 18px;
        padding: 6px;
        height: 56px;
        width: 56px;
        min-width: 56px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid $strokes;
        border-radius: 8px;
        color: $titles;
    }

    .extend,
    .current {
        cursor: default;

    }
}
