.map-ua {
    width: 100%;

    &__row {
        width: 100%;
        display: flex;
    }
}

.region {
    path {
        fill: #10100E;
        cursor: pointer;
        transition: .3s;
    }

    &.blue {
        path {
            stroke: #2174CF;
        }

        &:hover {
            path {
                fill: #102337;
            }
        }

        &.active {
            path {
                fill: #2174CF;
            }
        }
    }

    &.yellow {
        path {
            stroke: #F7D000;
        }

        &:hover {
            path {
                fill: #2E290D;
            }
        }

        &.active {
            path {
                fill: #F7D000;
            }
        }
    }
}

.region-title {
    margin-bottom: 45px;
    padding-right: 31px;
    font-weight: 500;
    font-size: 32px;
    line-height: 1.31;
    color: $titles;
}

.region-info {
    height: 675px;
    width: 460px;
    min-width: 460px;
    max-width: 460px;
    padding: 40px 8px 40px 40px;
    background: #141513;
    border-radius: 16px;
    margin-left: 65px;
    display: flex;
    flex-direction: column;
    flex-basis: max-content;
}

.region-list {
    @extend .scroll;
    overflow-y: auto;
    padding-right: 28px;
}

.region-item {
    padding-bottom: 35px;

    &__title {
        font-weight: 500;
        font-size: 24px;
        line-height: 1.67;
        color: $titles;
        margin-bottom: 8px;
    }

    &__cwt {
        font-size: 20px;
        line-height: 1.7;
        color: $accent;
    }

    &__address {
        @extend .region-item__cwt;
        color: #8A9385;
    }

    &__phone {
        margin: 0 0 15px;

        li {
            &::before {
                display: none;
            }
        }
    }
}
