@import "pagination";

.blog-row {
    display: flex;
    margin-bottom: 380px;
    flex-wrap: wrap;
}

.blog-sidebar {
    background: $inputs;
    border-radius: 20px;
    padding: 40px;
    border: 1px solid $body-text;
}

.blog-nav {
    display: flex;
    flex-direction: column;

    a {
        margin-bottom: 12px;
    }
}

.blog-sidebar {
    max-width: 350px;
    width: 100%;
    height: 100%;
    position: sticky;
    top: 165px;
    margin-bottom: 25px;
}

.article-grid {
    width: calc(100% - 450px);
    display: grid;
    grid-gap: 45px;
    grid-template-columns: 1fr 1fr;
    margin-left: auto;
}

.article {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    flex-shrink: 1;
    background: $inputs;
    border-radius: 15px;
    overflow: hidden;
    padding-bottom: 18px;

    &:hover {
        text-decoration: none;

        .article-title {
            color: $accent;
        }

        .article-description {
            color: $titles;
        }

        img {
            transform: scale(1.1);
        }
    }

    &-img {
        overflow: hidden;
        max-height: 460px;
    }

    img {
        transition: 1.5s;
    }

    &-title,
    &-description {
        padding: 0 24px;
        margin-bottom: 12px;
        transition: .3s;
    }

    &-description {
        color: $body-text;
        display: -webkit-box;
        -webkit-line-clamp: 5;
        -webkit-box-orient: vertical;
        overflow: hidden;
        margin-top: 0;
        line-height: 1.2;
    }
}

.pagination {
    position: relative;
    top: 130px;
    width: 100%;
    display: flex;
    justify-content: center;
    margin-left: 450px;
}

.single {
    .blog-row {
        flex-wrap: nowrap;
        margin-bottom: $section-margin;
    }

    .blog-sidebar {
 
        min-width: 350px;
 
        margin-bottom: 0;
        margin-left: 100px;

        .btn {
            width: 100%;
            margin-top: 12px;
        }
    }

    .post-item {
        margin-bottom: 100px;
    }
}

.column {
    display: flex;
    flex-direction: column;
}

.repost {
    display: flex;
    border-top: 1px solid #242424;
    border-bottom: 1px solid #242424;
    padding: 30px 0;

    .repost-title {
        margin: 0;
        line-height: 1;
        display: flex;
        align-items: center;
    }

    .socials {
        margin-left: 36px;
    }
}

.relise {
    font-style: italic;
    display: flex;
    align-items: center;
    margin-left: auto;
}


.single-navigation {
    display: flex;
    justify-content: space-between;
 
    margin: 70px -12px 0;
    a {
        max-width: 300px;
        display: flex;
        flex-direction: column;
        text-decoration: none;  
        transition: .3s;
        color: #8E938C;
        padding: 0 12px;
        >span {
            margin-bottom: 8px;
        }
        span+span {
            color: $titles;
            font-size: 14px;
            line-height: 1;
        }
        &:hover {
            span+span {
                text-decoration: underline;
                color: $accent-hover;
            }
        }
    }

    .next {
        text-align: right;
    }
}
